.cards-page {
    margin: 0 auto;
    max-width: 1200px;
}

.cards-container {
    max-width: 580px;
    /* padding: 20px; */
    margin: 0 auto;
    margin-top: 15px;
    border-radius: 8px;
    min-height: 80vh;
    background-color: #fff;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.1);
}

.content-title {
    font-size: 20px;
    font-weight: 700;
    margin: 0 15px 0 0;
    color: #ffffff;
}

.cards-content-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    border-radius: 8px 8px 0 0;
}

.cards-header-nav {
    display: flex;
}

.go-to-view {
    background-color: #525266;
    width: 50px;
    height: 32px;
    border-radius: 8px;
    position: relative;
    cursor: pointer;
    background-image: url(../images/icon/view.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 54%;
    margin-right: 15px;
}
.go-to-creat {
    background-color: #d9b216;
    width: 50px;
    height: 32px;
    border-radius: 8px;
    position: relative;
    cursor: pointer;
    background-image: url(../images/icon/creat.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 44%;
}

.add-card {
    background-color: #16d977;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    position: relative;
    cursor: pointer;
    background-image: url(../images/icon/add-button.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60%;
}

.container-content {
    padding: 20px;
}

.carousel-control-prev,
.carousel-control-next {
    width: 140px;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
    width: 30px;
    height: 30px;
    opacity: 0.2;
}

.carousel-control-prev-icon {
    background-image: url(../images/icon/left-swipe.png);
}

.carousel-control-next-icon {
    background-image: url(../images/icon/right-swipe.png);
}

/* адаптивность */
@media (max-width: 600px) {

    .carousel-control-prev,
    .carousel-control-next {
        width: 90px;
    }
}

@media (max-width: 500px) {

    .carousel-control-prev,
    .carousel-control-next {
        width: 35px;
    }
}

@media (max-width: 400px) {

    .carousel-control-prev,
    .carousel-control-next {
        width: 16px;
    }

    .carousel-control-prev {
        left: -10px;
    }

    .carousel-control-next {
        right: -10px;
    }

    .carousel-control-prev-icon,
    .carousel-control-next-icon {
        width: 15px;
        height: 15px;
        opacity: 0.2;
    }
}