.sets-page {
    margin: 0 auto;
    max-width: 1200px;
}

.sets-container {
    max-width: 580px;
    /* padding: 20px; */
    margin: 0 auto;
    margin-top: 15px;
    border-radius: 8px;
    min-height: 80vh;
    background-color: #fff;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.1);
}

.sets-title {
    font-size: 20px;
    font-weight: 700;
    margin: 0 15px 0 0;
    color: #ffffff;
}

.sets-content-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    background-color: #525266;
    border-radius: 8px 8px 0 0;
}

.add-set {
    background-color: #16d977;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    position: relative;
    cursor: pointer;
    background-image: url(../images/icon/add-button.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60%;
}

.container-content {
    padding: 20px;
}