.set-tile {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin: 15px auto;
    margin-bottom: 20px;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 2px 8px 2px rgba(0, 0, 0, 0.2);
    background-color: rgb(255, 255, 255);
    overflow: hidden;
    position: relative;
}

.set-popup {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #ffffff;
    z-index: 9;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.set-confirm-text {
    color: #525266;
    font-size: 24px;
    font-weight: 700;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    align-items: center;
}

.set-popup-icon {
    width: 40px;
    height: 40px;
    background: url(../images/icon/exclamation-mark.png) no-repeat center / contain;
    margin-right: 15px;
}

.set-popup-crose {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    background: url(../images/icon/close2.png) no-repeat center / contain;
    cursor: pointer;
}

.set-confirm-btn {
    margin: 0 10px !important
}

.set-info {
    flex: 1;
    margin-right: 15px;
}

.set-name {
    font-size: 20px;
    font-weight: 700;
    color: #525266;
    margin-bottom: 6px;
}

.edit-set {
    background-color: #f5f5fa;
    background-image: url('../images/icon/edit.png');
    background-repeat: no-repeat;
    background-size: 60%;
    background-position: center;
}

.edit-set:hover {
    background-color: #e9e9f2;
}

.delete-set {
    background-color: #f5f5fa;
    background-image: url('../images/icon/delete.png');
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: center;
}

.delete-set:hover {
    background-color: #e9e9f2;
}

.save-set {
    background-color: #f5f5fa;
    background-image: url('../images/icon/save.png');
    background-repeat: no-repeat;
    background-size: 60%;
    background-position: center;
    margin-right: 15px;
}

.save-set:hover {
    background-color: #e9e9f2;
}

.set-input {
    padding: 4px 8px;
    font-size: 20px;
    color: #8d8da6;
    border: 1px solid #8d8da6;
    border-radius: 4px;
    margin-bottom: 15px;
}

.set-input:focus {
    border-color: #525266 !important;
}

.btn-view {
    background-color: #525266 !important;
}

.btn-view:hover {
    background-color: #6D695A !important;
}

.set-tile-btn-icon-view,
.set-tile-btn-icon-creat {
    width: 24px;
    height: 24px;
    margin-right: 8px;
}

.set-tile-btn-icon-creat {
    background: url(../images/icon/creat.png) no-repeat center / 90%;
}

.set-tile-btn-icon-view {
    background: url(../images/icon/view.png) no-repeat center / 90%;
}

.set-edit-buttons {
    display: flex;
}

.set-tile .mobile {
    display: none;
}

.set-tile .desctop {
    display: block;
}

.btns_mobile {
    display: none;
}

.btns_mobile div {
    margin-bottom: 0;
}

.cards-count {
    font-size: 14px;
    color: #C2C2C2;
    margin-bottom: 11px;
}

@media (max-width: 550px) {
    .set-name {
        font-size: 18px;
        font-weight: 700;
        color: #525266;
        margin-bottom: 6px;
        min-width: 90px;
    }

    .set-input {
        padding: 3px 6px;
        font-size: 18px;
        color: #8d8da6;
        border: 1px solid #8d8da6;
        border-radius: 4px;
        margin-bottom: 15px;
        width: 100%;
    }
}

@media (max-width: 480px) {
    .btn-text {
        display: none;
    }

    .set-tile-btn-icon-view,
    .set-tile-btn-icon-creat {
        margin-right: 0;
    }

    div.btn-view,
    div.btn-creat {
        padding: 0;
        width: auto;
        padding: 0 14px;
    }

    .set-tile .mobile {
        display: block;
    }

    .set-tile .desctop {
        display: none;
    }

    .set-tile .btn-type2 {
        width: auto;
        padding: 0 14px;
    }
}